.footer {
    margin-top: 5rem;
    background-color: rgba(238, 85, 20, 0.04);
    &__container {
        width: 74rem;
        margin: 0 auto;
        padding-top: 8rem;
    }
    &__menu {
        width: 100%;
        padding-bottom: 2.5rem;
        &-text {
            line-height: 1.5rem;
            font-size: 1rem;
            margin: 0.6rem 0;
            a {
                text-decoration: none;
                color: var(--black-color);
            }
        }
        &-title {
            font-size: 1.5rem;
            margin-bottom: 2rem;
        }
        &-feedback {
            margin-top: 2rem;
        }
        &-input {
            width: 100%;
            padding: 0.9rem 2rem;
            border-color: rgba(203, 226, 255, 0.1);
            background-color: rgba(238, 85, 20, 0.04);
        }
        &-btn {
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
        }
        &-icon {
            margin: 0 1rem;
            &:nth-child(1) {
                margin-left: 0;
            }
        }
    }
    &__title {
        width: 100%;
        text-align: center;
        padding: 2rem 0;
        border-top: solid 0.2px rgba(128, 128, 128, 0.2);
    }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
    .footer {
        &__container {
            padding: 0 3rem;
            width: 100%;
        }
        &__menu {
            flex-direction: column;
            text-align: center;
            align-items: center;
            &-area {
                margin-top: 1rem;
                align-items: center;
            }
        }
    }
}

@media screen and (min-width: 300px) and (max-width: 767.5px) {
    .footer {
        &__container {
            padding: 0 3rem;
            width: 100%;
        }
        &__menu {
            flex-direction: column;
            text-align: center;
            align-items: center;
            &-area {
                margin-top: 1rem;
                align-items: center;
            }
            &-input {
                height: 3rem;
                font-size: 0.8rem;
                padding: 0.5rem;
            }
            &-btn {
                height: 3rem;
            }
        }
    }
}