.swiper__gallary {
    &-slide {
        position: relative;
        padding: 1rem 0;
        width: 11rem;
    }
    &-picture {
        margin-right: -2rem;
        width: 100%;
        width: 18.5rem;
        height: 13rem;
    }
    &-title {
        font-size: 1.4rem;
        margin-left: 2rem;
    }
    &-size {
        margin-top: 1rem;
        margin-left: 2rem;
    }
    &-price {
        margin-top: 1rem;
        margin-left: 2rem;
    }
    &-btn {
        margin-top: 1rem;
        margin-left: 2rem;
    }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
    .swiper__gallary {
        &-slide {
            align-items: center;
        }
    }
}

@media screen and (min-width: 300px) and (max-width: 767.5px) {
    .swiper__gallary {
        &-slide {
            padding-right: 2rem;
            align-items: center;
        }
    }
}